/* Buttons */
.btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  transition: all 0.5s ease;
  text-transform: uppercase;
  border-radius: 0;
  border-top-right-radius: 10px;
}

.btn span {
  display: block;
}

.btn:focus {
  outline: none;
}

.btn.active, .btn:active {
  box-shadow: none;
}

.btn-fill {
  background: #24aa65;
  border: 2px solid #24aa65;
}

.btn-fill:hover, .btn-fill:focus {
  background: transparent;
  color: #24aa65;
}

.btn-bordered {
  background: transparent;
  border: 2px solid #ffffff;
}

.btn-bordered:hover, .btn-bordered:focus {
  background: #ffffff;
  color: #24aa65;
}

.btn-black-bordered {
  color: #444444;
  background: transparent;
  border: 2px solid #444444;
}

.btn-black-bordered:hover, .btn-black-bordered:focus {
  background: #444444;
  color: #ffffff;
}

.btn-green-bordered {
  color: #2bbf71;
  background: transparent;
  border: 2px solid #2bbf71;
}

.btn-green-bordered:hover, .btn-green-bordered:focus {
  outline: unset;
  background: #2bbf71;
  color: #ffffff;
}

.btn-xs {
  font-size: 12px;
  padding: 6px 24px;
}

.btn-sm {
  padding: 6px 10px;
}

.btn-md {
  padding: 10px 30px;
}

.btn-lg {
  padding: 15px 25px;
  font-size: 16px;
}

.btn-rounded {
  border-radius: 50px;
}

.btn-success {
  background: #00caa4;
  border: 2px solid #00caa4;
}

.btn-success:hover, .btn-success:focus {
  background: transparent;
  color: #00caa4;
  border-color: #00caa4;
}

.btn-info {
  background: #59b2c9;
  border: 2px solid #59b2c9;
}

.btn-info:hover, .btn-info:focus {
  background: transparent;
  color: #59b2c9;
  border-color: #59b2c9;
}

.btn-warning {
  background: #ffd361;
  border: 2px solid #ffd361;
}

.btn-warning:hover, .btn-warning:focus {
  background: transparent;
  color: #ffd361;
  border-color: #ffd361;
}

.btn-danger {
  background: #ff5a5a;
  border: 2px solid #ff5a5a;
}

.btn-danger:hover, .btn-danger:focus {
  background: transparent;
  color: #ff5a5a;
  border-color: #ff5a5a;
}

/* ALERT BOX RESET */
.alert {
  padding: 10px 15px 10px 40px;
  border-radius: 3px;
  position: relative;
  text-align: left;
  letter-spacing: 1px;
}

.alert:before {
  font-family: FontAwesome;
  font-size: 15px;
  position: absolute;
  left: 15px;
}

.alert.alert-info {
  color: #ffffff;
  background-color: #59b2c9;
  border-color: #59b2c9;
}

.alert.alert-info:before {
  content: "\f05a";
  color: rgba(76, 76, 76, 0.6);
}

.alert.alert-warning {
  color: #6b6b6b;
  background-color: #ffd361;
  border-color: #ffd361;
}

.alert.alert-warning:before {
  content: "\f071";
  color: rgba(76, 76, 76, 0.6);
}

.alert.alert-danger {
  color: #ffffff;
  background-color: #ff5a5a;
  border-color: #ff5a5a;
}

.alert.alert-danger:before {
  content: "\f05e";
  color: rgba(76, 76, 76, 0.6);
}

.alert.alert-success {
  color: #f7f7f7;
  background-color: #00caa4;
  border-color: #00caa4;
}

.alert.alert-success:before {
  content: "\f058";
  color: rgba(76, 76, 76, 0.6);
}

